.App {
  text-align: center;
  font-family: "Courier New", monospace;
  width: 100%;
}
h1 {
  font-weight: normal;
  font-size: 42px;
}
.passwords {
  list-style: none;
  padding: 0;
  font-size: 32px;
  margin-bottom: 2em;
}
.more {
  font-size: 32px;
  font-family: "Courier New", monospace;
  border: 2px solid #000;
  background-color: #fff;
  padding: 10px 25px;
}
.more:hover {
  background-color: #000000;
  color: #fff;
}
.more:active {
  background-color: #ffefa9;
}
